.bt-wrapper,.transparency{background-color:rgba(135,123,106,.48)}body,html{color:#322f2f}body::-webkit-scrollbar{display:none}#cover{position:relative;height:100vh;background-repeat:no-repeat;background-position:center center;background-attachment:scroll;-webkit-background-size:cover;-moz-background-size:cover;-o-background-size:cover;background-size:cover;font-family:Lora,serif}@media all and (orientation:landscape){#cover{background-image:url(../images/cover-raw-land.jpg)}}@media all and (orientation:portrait){#cover{background-image:url(../images/cover-raw-port.jpg)}}@media all and (max-width:320px) and (orientation:portrait){#cover{background-image:url(../images/cover-320-port.jpg)}}@media all and (max-width:640px) and (orientation:landscape){#cover{background-image:url(../images/cover-640-land.jpg)}}@media all and (min-width:321px) and (max-width:425px) and (orientation:portrait){#cover{background-image:url(../images/cover-425-port.jpg)}}@media all and (min-width:426px) and (max-width:768px) and (orientation:portrait){#cover{background-image:url(../images/cover-768-port.jpg)}}@media all and (min-width:641px) and (max-width:1024px) and (orientation:landscape){#cover{background-image:url(../images/cover-1024-land.jpg)}}@media all and (min-width:769px) and (max-width:1024px) and (orientation:portrait){#cover{background-image:url(../images/cover-1024-port.jpg)}}@media all and (min-width:1025px) and (max-width:1280px) and (orientation:landscape){#cover{background-image:url(../images/cover-1280-land.jpg)}}@media all and (min-width:1281px) and (max-width:2048px) and (orientation:landscape){#cover{background-image:url(../images/cover-2048-land.jpg)}}#cover>header{position:absolute;top:50%;transform:translateY(-50%);width:100%}.transparency{padding:1px 1em}.title{font-size:3em;text-shadow:2px 2px 10px}.bt-nav a,.subtitle{text-shadow:1px 1px 12px #a29182}.subtitle{font-style:italic;font-weight:lighter;font-size:1.4em}.bt-wrapper{display:flex;width:100%}.bt-wrapper.above{margin-bottom:1em}.bt-wrapper.below{margin-top:1em}.bt-cover{margin:0 auto;padding:1em .3em;border-bottom:1px solid #d8d3cc}.bt-cover a{font-size:.9em;text-decoration:none;color:#d8d3cc}.nav-hidden{display:none}.cst-nbar{position:absolute;top:2em;left:2em}#search,.tipo-precio{position:relative}.cst-nbar ul{list-style:none;margin:0;padding:0}.cst-nbar li{float:left}.bt-nav{padding:0 .3em 1em;margin:0 1em;border-bottom:1px solid #322f2f}.bt-nav a{text-decoration:none;color:#322f2f}.bt-nav.active{border-bottom-width:thick}.bt-nav.active a{font-weight:700;font-style:oblique}@media all and (min-width:768px){.cst-nbar{display:inherit}.bt-cover{display:none}}@media all and (max-width:991px),(orientation:portrait){.cst-nbar{top:12%;left:50%;transform:translateX(-50%)}}#resultados,#search-form{margin-top:1.5em;margin-bottom:1.5em}#bt-grp-tipoTrans{left:50%;transform:translateX(-50%);margin-bottom:1em}.anuncio{padding:0}.anuncio img{width:100%}.anuncio .breadcrumb{margin-bottom:0;border-radius:0;font-size:smaller}.tipo-precio-inner{position:absolute;background-color:rgba(17,17,17,.64);color:#fff;padding:.5em;transform:translateY(-100%)}.anuncio-detalles{margin-left:0;margin-right:0;padding-bottom:1em}.anuncio-mas-detalles span{font-weight:700;padding-right:.5em}.anuncio-titulo{margin-top:0;font-size:1.5em}